/**
 * 获取微信小程序顶部胶囊（右上角操作区）的信息
 * @returns {Object} 包含胶囊高度、宽度、位置等信息的对象
 */
export function getMenuButtonInfo() {
  // 获取胶囊按钮的布局信息
  const menuButtonRect = uni.getMenuButtonBoundingClientRect();
  // 获取系统信息（包含状态栏高度等）
  const systemInfo = uni.getSystemInfoSync();
  
  return {
    // 胶囊本身的尺寸和位置
    height: menuButtonRect.height,
    width: menuButtonRect.width,
    top: menuButtonRect.top,
    left: menuButtonRect.left,
    right: menuButtonRect.right,
    bottom: menuButtonRect.bottom,
    // 状态栏高度
    statusBarHeight: systemInfo.statusBarHeight,
    // 计算导航栏高度（状态栏高度 + 胶囊高度 + 胶囊上下到边界的距离）
    navBarHeight: systemInfo.statusBarHeight + menuButtonRect.height + (menuButtonRect.top - systemInfo.statusBarHeight) * 2
  };
}